@import url(https://fonts.googleapis.com/css2?family=Cormorant+SC:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cormorant+SC:wght@600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.weatherData {
    display: grid;
    grid-area: W;

    font-family: 'Cormorant SC', serif;
    font-size: 30px;
    color: #463dff;
    text-shadow: 1px 1px 1px black, 0 0 0em black;

}
.weatherData_data {
    list-style-type: none;

}



.error {
    font-size: 50px;
    color: red;
    justify-content: center;
    transition: 0.5s;


}




.info_info_block__2ocI5 {
    padding: 0;
    display: grid;
    group-area: Info;



}

.info_appTextOne__10D7C {
    font-family: 'Cormorant SC', serif;
    font-size: 100px;
    color: #463dff;
    text-shadow: 1px 1px 2px black, 0 0 1em black;

}
.info_appTextTwo__tmEY6 {
    font-family: 'Cormorant SC', serif;
    font-size: 50px;
   margin-bottom: 50px;
    color: #463dff;
    text-shadow: 1px 1px 2px black, 0 0 1em black;
}
.App {
  height: 100%;
  width: 100%;

  margin: auto;
  background: url(/test-task-weather/static/media/logo.f0ed8e4d.jpg) 100%, 100%, no-repeat;
  display: grid;
  grid-template-columns: repeat(3, 1fr);

  grid-auto-rows: minmax(150px, auto);


}


.Info {
  margin-left: 1%;
  grid-column: 1 / 2;
  grid-row: 1;
}
.Modal {

  grid-column: 2 / 1;
  grid-row: 2 / 2;
}
.Weather {

  margin-left: 1%;
  grid-column: 1;
  grid-row: 3 / 5;
}


.modal {
    text-align: center;

     height: 100%;
     width: 100%;
     background-color: rgba(0,0,0,0.1);
     position: fixed;
     top: 0;
     left: 0;
     display: flex;
     align-items: center;
     justify-content: center;
     transform: scale(0);
     transition: 0.5s;


 }

 .modal.active {
     transform: scale(1);

 }
 .modalContent {
     padding: 20px;
     border-radius: 12px;
     background-color: #81dcf8;
     height: 300px;
     width: 400px;
     box-shadow: 1px 1px 2px black, 0 0 1em black;

 }

 .btn {
     transition: 0.5s;
 }
 .btn.active {
     transform: scale(1);
 }

 button {
     margin-left: 1%;
     font-size: 40px;
     font-family: 'Cormorant SC', serif;
     border-radius: 12px;
     background-color: #086bf2;
     color: #0921da;

 }


.preloader {
   margin-left: 8%;
    margin-top: 8%;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
}
